@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600&display=swap");
@keyframes move_wave {
  0% {
    transform: translateX(0) translateZ(0) scaleY(1);
  }
  50% {
    transform: translateX(-25%) translateZ(0) scaleY(0.55);
  }
  100% {
    transform: translateX(-50%) translateZ(0) scaleY(1);
  }
}
@keyframes move-from-top-to-bottom {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes move-from-left-to-right {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes move-from-right-to-left {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes move-content {
  0% {
    transform: translateY(25px);
  }
  100% {
    transform: translatey(0);
  }
}
@keyframes move-arrow {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0);
  }
}
body {
  overflow-x: hidden;
  width: 100%;
  scroll-behavior: smooth;
  height: 100%;
  margin: 0 auto;
  background: linear-gradient(to top, #86377b 20%, #27273c 80%);
}

.container {
  height: 100%;
  padding: 0 30px;
  margin: 0 auto;
}

.row-content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 15px;
  justify-content: space-between;
}

a {
  text-decoration: none;
  font-size: 15px;
  line-height: 15px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  letter-spacing: 0.02em;
  cursor: pointer;
  z-index: 10;
}

.ng-invalid,
.ng-touched,
.alert-danger {
  font-size: 15px;
  line-height: 15px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: red;
  letter-spacing: 0.02em;
  margin-top: 10px;
}

.button {
  z-index: 50;
  margin-top: 30px;
  width: 200px;
  height: 40px;
  font-size: 18px;
  line-height: 18px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-style: normal;
  color: #0F161E;
  letter-spacing: 0.02em;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50px;
  overflow: hidden;
  position: relative;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  box-shadow: 0 0 6px 0 #ffffff;
}
.button:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
}
.button:active {
  transform: translateY(-2px);
}

.layout-wrapper {
