  justify-content: center;
  align-items: center;
  position: relative;
  outline: none;
  cursor: pointer;
  z-index: 10;
}
.layout-wrapper .header .header-navigation .header-navigation-link:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
