  margin-top: 15px;
  width: 190px;
  cursor: pointer;
  font-size: 25px;
  line-height: 25px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  letter-spacing: 0.02em;
  background: transparent;
  outline: none;
  border: none;
}
.layout-wrapper .footer .footer-row .footer-item .footer-subscribe-button .subscribe-button-arrow {
