  width: 25px;
  height: 1px;
  background: #ffffff;
}
.layout-wrapper .footer .footer-row .footer-item .footer-subscribe-button .subscribe-button-arrow .subscribe-button-arrow-top {
  position: absolute;
  top: -4.5px;
  right: -2px;
  border-radius: 2px;
  width: 13px;
  height: 1px;
  transform: rotate(45deg);
  background: #ffffff;
}
.layout-wrapper .footer .footer-row .footer-item .footer-subscribe-button .subscribe-button-arrow .subscribe-button-arrow-bottom {
  position: absolute;
  bottom: -4.5px;
  right: -2px;
  border-radius: 2px;
  width: 13px;
  height: 1px;
  transform: rotate(-45deg);
  background: #ffffff;
}
.layout-wrapper .footer .footer-row .footer-item .footer-subscribe-button:hover .subscribe-button-arrow {
  animation: 1s move-arrow;
}
.layout-wrapper .footer .waveWrapper {
  display: block;
  overflow: hidden;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: -120px;
  margin: auto;
  z-index: -10;
}
.layout-wrapper .footer .waveWrapperInner {
  position: absolute;
  width: 100%;
  overflow: hidden;
  height: 100%;
  bottom: 0;
}
.layout-wrapper .footer .bgTop {
  z-index: -15;
  opacity: 0.7;
}
.layout-wrapper .footer .bgMiddle {
  z-index: -10;
  opacity: 0.85;
}
.layout-wrapper .footer .bgBottom {
  z-index: -5;
  opacity: 0.5;
}
.layout-wrapper .footer .wave {
  position: absolute;
  left: 0;
  width: 200%;
  height: 100%;
  background-repeat: repeat no-repeat;
  background-position: 0 bottom;
  transform-origin: center bottom;
}
.layout-wrapper .footer .waveTop {
  background-image: url("../assets/img/wave-top.svg");
}
.layout-wrapper .footer .waveAnimation .waveTop {
  animation: move-wave 3s;
  -webkit-animation: move-wave 3s;
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}
.layout-wrapper .footer .waveMiddle {
  background-image: url("../assets/img/wave-mid.svg");
}
.layout-wrapper .footer .waveAnimation .waveMiddle {
  animation: move_wave 10s linear infinite;
}
.layout-wrapper .footer .waveBottom {
  background-image: url("../assets/img/wave-bot.svg");
}
.layout-wrapper .footer .waveAnimation .waveBottom {
  animation: move_wave 7s linear infinite;
}

