.wrapper-popup {
  display: block;
  position: absolute;
  top: 0;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background: #0F161E;
  border-radius: 0;
  transition: 0.5s width ease 0.3s, 0.5s;
  left: 0;
}
.wrapper-popup div {
  color: white;
}

.wrapper-popup-close {
  display: none;
}

.popup-open {
  padding: 30px;
  position: fixed;
  top: 30%;
  left: 0;
  background: #0F161E;
  content: "";
  width: calc(100vw - 60px);
  height: 300px;
  z-index: 100;
  transition: 0.5s all ease;
}
.popup-open .popup-thank {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.popup-open .popup-thank .popup-thank-title {
  font-size: 23px;
  line-height: 23px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  letter-spacing: 0.02em;
}
.popup-open .popup-open-description {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.popup-open .popup-open-description .popup-title {
  font-size: 20px;
  line-height: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-style: normal;
  color: #ffffff;
  letter-spacing: 0.02em;
  margin-bottom: 15px;
}
.popup-open .popup-open-description input {
  border: none;
  width: 300px;
  margin-bottom: 15px;
  background: transparent;
  border-bottom: 1px solid #ffffff;
  outline: none;
  font-size: 15px;
  line-height: 12px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  letter-spacing: 0.02em;
}
.popup-open .popup-open-description .button {
  margin-top: 20px;
}

.close {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  right: 15px;
  cursor: pointer;
}
.close:before, .close:after {
  position: absolute;
  content: "";
  width: 1px;
  height: 17px;
  top: 35%;
  right: 50%;
  background: #ffffff;
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}

#map {
  width: 100%;
  height: 500px;
  margin: 50px 0;
}

@media (max-width: 950px) {
  .open-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    height: 40px;
    min-width: 40px;
    box-shadow: 2px 2px 0px #00D1FF, -2px -2px 0px #FA0BFF;
    border-radius: 3px;
  }
  .open-menu span {
    transition: all 0.5s ease-in-out;
    position: relative;
    height: 1px;
    width: 20px;
    background: #ffffff;
    border-radius: 1px;
  }
  .open-menu span:before {
    content: "";
    top: -9px;
    left: 0;
    position: absolute;
    height: 1px;
    width: 20px;
    background: #ffffff;
    border-radius: 1px;
    transition: all 0.5s ease;
  }
  .open-menu span:after {
    content: "";
    bottom: -9px;
    left: 0;
    position: absolute;
    height: 1px;
    width: 20px;
    background: #ffffff;
    border-radius: 1px;
    transition: all 0.5s ease;
  }

  .closeMenu span {
    background: transparent;
  }
  .closeMenu span:before {
    transform: rotate(45deg);
    top: 0px;
  }
  .closeMenu span:after {
    transform: rotate(-45deg);
    bottom: 0px;
  }

  .open-menu-bg {
    position: absolute;
    top: 100px;
    left: 0;
    background: #0F161E;
    content: "";
    width: 100%;
    height: 225px;
  }

  .open-menu-bg-hidden {
    display: none;
  }

  .header-navigation {
    display: none !important;
  }

  .header-navigation-hidden {
    position: absolute;
    top: 100px;
    width: calc(100% - 30px);
  }
  .header-navigation-hidden li {
    text-decoration: none;
    outline: none;
    list-style-type: none;
  }
  .header-navigation-hidden li a,
.header-navigation-hidden li div {
    padding: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    line-height: 15px;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-style: normal;
    color: #ffffff;
    letter-spacing: 0.02em;
  }

  .main-banner .main-banner-row {
    width: 100% !important;
  }
  .main-banner .main-banner-row .main-banner-text {
    text-align: center;
  }
  .main-banner img {
    display: none;
  }

  .row-content {
    grid-template-columns: 1fr !important;
  }

  .contact-row {
    grid-template-columns: 1fr !important;
  }

  .footer-item {
    text-align: center;
    align-items: center;
  }

  .footer-main-text {
    text-align: center;
  }

  .waveWrapper {
    top: -10px !important;
  }

  .waveBottom {
    background-size: cover;
  }

  .waveMiddle {
    background-size: cover;
  }

  .waveTop {
    background-size: cover;
  }
}